import { ExclamationCircleOutlined } from '@ant-design/icons';
import React, { useState } from 'react';
//import 'bootstrap/dist/css/bootstrap.css';
import { Breadcrumb, Layout, theme, FloatButton, Divider } from 'antd';
import axios from 'axios'
import { toast } from 'react-toastify';
import { Button, Space,Select, Form, Input, Modal,Checkbox  } from 'antd';
import { BulbTwoTone,BulbOutlined } from '@ant-design/icons';

const { TextArea } = Input;
const { Header, Content, Sider } = Layout;
 
const layout = {
  labelCol: {
    span: 3,
  },
  wrapperCol: {
    span: 20,
  },
};


const LocalizedModal = ({product_id}) => {

  
  const [modal, contextHolder] = Modal.useModal();

  const {
    
    token: { colorBgContainer },
  } = theme.useToken();
 
const [form] = Form.useForm();
const [checked, setChecked] = useState(true);
const onChange = (e) => {
  console.log('checked = ', e.target.checked);
  setChecked(e.target.checked);
};

    const handleSubmit = () => {
 
      console.log( product_id);
 

        const payload = {
          "annonymous" : checked  ,
          "title" : form.getFieldValue('title')  ,
          "description" : form.getFieldValue('desc'),

        }

console.log(payload);

        var user_access_token = localStorage.getItem("user_access_token");
       
          axios.post(process.env.REACT_APP_API_URL + "product/"+ product_id+"/idea", payload, {
         headers: {
           Authorization: "Bearer "+user_access_token,
         }})
         .then(function (res) {
           console.log(res)
           if(res.status === 200){
             toast.success("Create Idea successfully");
             hideModal();
            //  window.location.href = "../product-detail/"+product_id

             }else {
               toast.error("Something went wrong. Please try again!!");
              
             }
         })
         .catch(function (error) {
           console.log(error);
         });
         
        
        }

  const [open, setOpen] = useState(false);

  const showModal = () => {
    setOpen(true);
  };

  const hideModal = () => {
    setOpen(false);
  };

  return (
    <>
      <Button icon={<BulbTwoTone />}  onClick={showModal}>
      Got an idea?
      </Button>
      <Divider/>
      <Modal

       
        open={open}
        onOk={ handleSubmit}
        onCancel={hideModal}
        okText="Submit"
        cancelText="Cancel"
        width={500}
        bodyStyle={{height: 260, borderRadius:5 }}
      >
         <BulbOutlined  style={{ fontSize: '45px', color: 'gold' }}   /> <Space   > <h3 className='h3-IdeaHub'> Got an idea?</h3></Space> 
     <Form       
       {...layout}
       style={{
        maxWidth: 500,
        textAlign:"right" 
      }}

    

        form={form} name="control-hooks"  >

 
      <Form.Item
        name="title"
        label="Title"
      >
        <Input placeholder="A breif title" />
      </Form.Item>
      
    
      <Form.Item
        name="desc"
        label="Details"
      >
        <TextArea showCount
      maxLength={250} rows={4} placeholder="Tell us more.."/>

      </Form.Item> 
  
      <Checkbox    onChange={onChange}>Keep me Annonymous</Checkbox>

      <Form.Item
      wrapperCol={{
        ...layout.wrapperCol,
        offset: 8,
      }}
    >
    </Form.Item>
    </Form>

    
      </Modal>
    </>
  );
};

const App = ({domElement}) => {

  return (
    <>
      <Space>
        <LocalizedModal domElement={domElement} />

      </Space>
  
    </>
  );
};

export default App;